<template>
    <div id="app">
        <v-app>
            <router-view></router-view>
        </v-app>
    </div>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: 'app',
        mounted() {
            const currentUser = firebase.auth().currentUser;
            if (!currentUser && this.$route.path !== '/login') {
                this.$router.push('/login');
            }
        }
    }
</script>

<style>
    @import "css/main.css";

    #app {
        font-family: 'Roboto Regular', sans-serif;
        background: #eee;
    }
</style>