<template>
    <v-app>
        <header class="app-title text-center">
            <h3>Register</h3>
            <v-divider></v-divider>
        </header>

        <section class="auth">
            <v-form class="auth-form" v-model="validation" @submit="submit">
                <v-subheader class="subtitle-1">Enter your credentials below: </v-subheader>
                <v-text-field
                        class="login-form__input"
                        v-model="form.email"
                        :rules="emailValidation"
                        label="email"
                        placeholder="email"
                        solo
                ></v-text-field>
                <v-text-field
                        class="login-form__input"
                        v-model="form.name"
                        :rules="required"
                        label="name"
                        placeholder="name"
                        solo
                ></v-text-field>
                <v-text-field
                        class="login-form__input"
                        v-model="form.phone"
                        :rules="phoneValidation"
                        label="phone"
                        placeholder="phone"
                        solo
                ></v-text-field>
                <v-text-field
                        class="login-form__input"
                        v-model="form.password"
                        :rules="required"
                        label="password"
                        placeholder="password"
                        type="password"
                        solo
                ></v-text-field>
                <div class="auth-link text-right">
                    <router-link to="/login">Already have an account?</router-link>
                </div>
                <v-btn
                        :disabled="!validation"
                        :loading="loading"
                        @click="submit"
                >
                    Register</v-btn>
            </v-form>
        </section>
    </v-app>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "the-register",
        data() {
            return {
                form: {
                    email: null,
                    password: null
                },
                validation: false,
                emailValidation: [
                    v => !!v || 'E-mail is required',
                    v => /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'E-mail must be valid',
                ],
                phoneValidation: [
                    v => !!v || 'Phone is required',
                    // v => /^((\+380)+([0-9]){9})$/.test(v) || 'Phone must be valid',
                ],
                required: [
                    v => !!v || 'Field is required',
                ],
                loading: false
            }
        },
        mounted() {
            document.addEventListener("deviceready", this.backClose, false);
        },
        destroyed() {
            document.removeEventListener("deviceready", this.backClose, false);
        },
        methods: {
            async submit() {
                this.loading = true;
                try {
                    const response = await firebase.auth()
                        .createUserWithEmailAndPassword(this.form.email, this.form.password);
                    console.log(response);

                    const currentUser = await firebase.auth().currentUser;
                    console.log(currentUser);

                    const addInfo = await currentUser.updateProfile({
                        displayName: this.form.name,
                        phoneNumber: this.form.phone
                    });

                    const updatedUser = await firebase.auth().onAuthStateChanged(
                        async (user) => {
                            console.log('user', user);
                            await this.$store.dispatch('setUsername', user.displayName);
                            this.$router.push('/');
                        }
                    );

                    this.loading = false;
                } catch (error) {
                    this.loading = false;
                    console.log(error);
                }
            },
            
            backClose() {
                document.addEventListener("backbutton", function (e) {
                    e.preventDefault();
                    navigator.app.exitApp();
                }, false);
            },
        },
    }
</script>

