<template>
    <v-app>
        <header class="app-title text-center">
            <h3>Log in</h3>
            <v-divider></v-divider>
        </header>

        <section class="auth">
            <v-form class="auth-form" v-model="validation" @submit="submit">
                <v-subheader class="subtitle-1">Enter your credentials below:</v-subheader>
                <v-text-field
                        class="login-form__input"
                        v-model="form.email"
                        :rules="emailValidation"
                        label="email"
                        placeholder="email"
                        solo
                ></v-text-field>
                <v-text-field
                        class="login-form__input"
                        v-model="form.password"
                        :rules="passwordValidation"
                        label="password"
                        placeholder="password"
                        type="password"
                        solo
                ></v-text-field>
                <div class="auth-link text-right">
                    <router-link to="/register">Don't have an account?</router-link>
                </div>
                <v-btn
                        :disabled="!validation"
                        :loading="loading"
                        @click="submit"
                >
                    Login
                </v-btn>
                <v-btn @click="quit">quit</v-btn>
            </v-form>
        </section>
    </v-app>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "the-login",
        data() {
            return {
                form: {
                    email: null,
                    password: null
                },
                validation: false,
                emailValidation: [
                    v => !!v || 'E-mail is required',
                    v => /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'E-mail must be valid',
                ],
                passwordValidation: [
                    v => !!v || 'Password is required',
                ],
                loading: false
            }
        },
        mounted() {
            document.addEventListener("deviceready", this.backClose, false);
        },
        destroyed() {
            document.removeEventListener("deviceready", this.backClose, false);
        },
        methods: {
            async submit() {
                this.loading = true;
                try {
                    await firebase.auth().signInWithEmailAndPassword(this.form.email, this.form.password);
                    const currentUser = await firebase.auth().currentUser;
                    console.log(currentUser);
                    await this.$store.dispatch('setUsername', currentUser.displayName);
                    this.$router.push('/');
                    this.loading = false;
                } catch (error) {
                    this.loading = false;
                    console.log(error);
                    throw error;
                }

            },
            backClose() {
                document.addEventListener("backbutton", function (e) {
                    e.preventDefault();
                    navigator.app.exitApp();
                }, false);
            },
        },

    }
</script>

